import React, { useEffect, useState } from "react";
import Project from "./Project";

import { Link } from "react-router-dom";

const Projects = () => {
  /*
    URL projects
    https://portfolio-strapi-heroku.herokuapp.com/projects
    - Guardar en un estado todos los proyectos obtenidos
    - Listarlos
    - Poner Link to= a cada proyecto y usar campo ${name} de ese pryecto para URL
    - Cuando se cargue ese componente project/${name} se obtiene de nuevo todos los proyectos
      se hace match con el correspondiente a su name y se lista todos sus campos.
  */
  const [projects, setProjects] = useState([]);

  const getProjects = () => {
    const URL = `https://portfolio-strapi-heroku.herokuapp.com/projects`;
    fetch(URL)
      .then((response) => response.json())
      .then((response_json) => {
        setProjects(response_json);
      });
  };

  useEffect(() => {
    getProjects();
  }, []);

  return (
    <section className="container section">
      <div className="u-padding-right">
        <h2 className="title" data-aos="fade-right" data-aos-duration="1000">
          Proyectos realizados
        </h2>
        <p className="u-margin-txt" data-aos="fade-up" data-aos-duration="1000">
          It is a long established fact that a reader will be distracted by the
          readable content of a page when looking at its layout. The point of
          using Lorem Ipsum is that it has a more-or-less normal distribution
        </p>
      </div>
      <div className="columns is-multiline">
        {projects.map((project) => (
          <Project key={project._id} project={project} />
        ))}
      </div>
    </section>
  );
};

export default Projects;
